<template>
    <div>
        <CContentHeader :isShowIcon="true" content="修改角色" />
        <div class="c_create_roles_main">
            <h3>名称</h3>
            <div class="c_create_roles_total">
                <div class="c_create_roles_input c_input_color">
                    <span>角色名称：</span>
                    <!-- <el-input class="create_roles_input_frame" v-model="input" placeholder="请输入内容"></el-input> -->
                    <input type="text" class="c_create_roles_input_frame" placeholder="请输入名称" v-model="userName">
                </div>
                <div class="c_create_roles_input c_input_color">
                    <span>角色别名：</span>
                    <!-- <el-input class="create_roles_input_frame" v-model="input" placeholder="请输入内容"></el-input> -->
                    <input type="text" class="c_create_roles_input_frame" placeholder="请输入别名" v-model="alias">
                </div>
            </div>
            <div class="c_create_roles_describe_box">
                <div class="c_create_roles_describe c_input_color">
                    <span>角色描述：</span>
                    <el-input type="textarea" :autosize="{ minRows: 3}" placeholder="请输入" v-model="description"></el-input>
                </div>
            </div>
            <h3>权限</h3>
            <div class="c_create_roles_power">
                <span>角色权限：</span>
                <el-button @click="dialog" class="c_create_roles_power_but" type="info" plain>选择权限<i class="el-icon-arrow-right el-icon--right"></i></el-button>
            </div>
            <div class="c_create_roles_choice">
                <span>已选权限：</span>
                <div class="c_create_roles_power_text">
                    <div v-for="item in setPowerList" :key="item.id" class="c_create_roles_power_name">
                        {{item.name}}
                    </div>
                    <!-- <div class="c_create_roles_power_name">
                        数据总览
                    </div> -->

                </div>
            </div>
            <!-- 底部按钮 -->
            <div class="c_create_roles_foot">
                <el-button class="c_create_roles_foot_btn" @click="addRoleBtn" type="primary">完成</el-button>
            </div>
        </div>

        <!-- 弹框组件 -->
        <div class="c_elastic_frame">
            <PowerDeploy title="选择菜单权限" twoTitle="角色将拥有以下菜单权限" :dialogShow.sync="dialogShow" :powerList="powerList" @submit="submitBtn" @reset="resetBtn"></PowerDeploy>
            <!-- @cancel="cancelBtn"  :menuList="menuList"-->
        </div>
    </div>
</template>

<script>
    //引入弹框
    import PowerDeploy from "../../components/c_powerDeploy.vue"
    import { setTimeout } from 'timers';
    export default {
        data() {
            return {
                userName: '',
                description: '', //描述
                // menus: [],
                alias: '',  //别名
                dialogShow: false,
                powerList: [], //给子组件传的权限
                setPowerList: [], //已选的权限
                roleid: '',   //角色id
                company_id: 'cp_001',   //企业id
                user_id: 'user_001'  //用户id
            };
        },
        mounted(){
            console.log(this.$route.params.id);
            this.roleid = this.$route.params.id
            this.getOne()
        },
        methods: {
            submitBtn(item) { //接收子组件传的参数
                this.setPowerList = JSON.parse(JSON.stringify(item));
                console.log(this.setPowerList);
                
                this.dialogShow = false;
            },
            resetBtn() { //重置按钮
                this.dialogShow = false;
            },
            dialog() { //权限配置按钮
                this.dialogShow = true;

                if (this.setPowerList.length === 0) {
                    return;
                }
                this.powerList = JSON.parse(JSON.stringify(this.setPowerList));
            },
            async getOne (){
                console.log(this.user_id);
                
                let res = await this.$api.authorized.getOne({
                    company_id: this.company_id,
                    user_id: this.user_id,
                    roleid: this.roleid
                })
                console.log('获取到了。。。');
                console.log(res);
                if(res.statusCode !== 75200){
                    return  this.$zero.ToastWarning("服务异常，请稍后再试");
                }
                this.userName = res.role._name;
                this.alias = res.role.alias;
                this.description = res.role.description;
                // this.setPowerList = res.role.menus
                for (let i = 0; i < res.role.menus.length; i++) {
                    this.setPowerList[i] = {
                        name: res.role.menus[i]._name,
                        id: res.role.menus[i]._id,
                        level: res.role.menus[i].level,
                        serial: res.role.menus[i].serial,
                        parent_id: res.role.menus[i].parent_id
                    }
                }

            },
            async addRoleBtn() {
                let menus = [];
                for (let i = 0; i < this.setPowerList.length; i++) {
                    menus.push(this.setPowerList[i].id)
                    
                }

                let res = await this.$api.authorized.addMenu({
                    menus: menus,
                    role_id: this.roleid
                })
                console.log(res);
                
                if(res.statusCode === 75200){
                    this.$zero.ToastSuccess('修改成功！');
                    setTimeout(()=>{
                        window.location.href = "/power";
                    },2000)
                }else{
                     return  this.$zero.ToastWarning("服务异常，请稍后再试");
                }
            }
        },
        beforeDestroy(){
            this.$zero.eventBus.$off("addBookLoadMore");
            this.$zero.eventBus.$off("departmentItem");
        },
        components: {
            PowerDeploy //注册遮罩层组件
        }

    };
</script>

<style scoped>
    .c_create_roles_main {
        margin: 20px 40px 0px;
        padding: 20px 0;
        background-color: #fff;
    }

    .c_create_roles_main>h3 {
        margin: 0 30px;
        line-height: 40px;
        font-size: 18px;
        font-weight: bold;
        border-bottom: 2px solid #ededed;
    }

    .c_create_roles_total {
        display: flex;
        padding: 25px 35px 0px;

    }


    .c_create_roles_input {
        display: flex;
        font-size: 14px;
        color: #999;
        /* width: 655px; */
        width: 41%;
        align-items: center;
        margin-right: 20px;
    }

    /* 输入框样式 */
    .c_create_roles_input_frame {
        /* width: 495px; */
        width: 78%;
        border: 1px solid #e6e6e6;
        color: #000;
        height: 38px;
        line-height: 38px;
        box-sizing: border-box;
        padding: 0 15px;
        margin-left: 10px;
    }

    .c_create_roles_describe_box {
        padding: 25px 35px 35px;
    }

    .c_create_roles_describe {
        width: 41%;
        display: flex;
        font-size: 14px;
        color: #999;
    }

    .c_create_roles_describe>span {
        /* width: 74px; */
        margin-right: 10px;
        margin-top: 10px;
    }

    .c_create_roles_describe .el-textarea {
        width: 78%;
        /* color: #333; */
    }
    /* .c_create_roles_describe .el-textarea .el-textarea__inner{
        color: #333;
    } */
    

    .c_create_roles_power,
    .c_create_roles_choice {
        display: flex;
        padding: 25px 35px 0;
        align-items: center;
        font-size: 14px;
        color: #999;
    }

    .c_create_roles_power_but.is-plain {
        color: #333;
        border-color: #e6e6e6;
        background-color: #f2f2f2;
        border-radius: 0px;
        padding: 11px 28px;
        margin-left: 10px;
        font-weight: bold;
    }

    /* .c_create_roles_choice>span {
        position: relative;
        top: -76px;
    } */

    .c_create_roles_power_text {
        /* width: 500px; */
        /* height: 180px; */
        /* border: 1px solid #e6e6e6; */
        min-height: 30px;
        margin-left: 10px;
        display: flex;
        flex-wrap: wrap;
    }

    .c_create_roles_power_name {
        background-color: #2f78ef;
        color: #fff;
        font-size: 14px;
        width: 100px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        padding: 0px 10px;
        margin-right: 10px;
        /* margin-bottom: 10px; */
    }

    /* 底部按钮 */
    .c_create_roles_foot {
        margin-top: 90px;
        border-top: 1px solid #d9d9d9;
        height: 100px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .c_create_roles_foot_btn {
        font-size: 16px;
        background-color: #2f78ef;
        border-color: #2f78ef;
        border-radius: 0px;
        padding: 12px 54px;
    }
</style>